@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  background-color: black;
}

.message {
  margin-top: -25px;
}

p,
button,
input,
label {
  font-family: 'VT323', monospace;
  font-size: 2em;
  color: green;
}

input {
  margin-left: 7px;
  background: transparent;
  border: none;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 10px;
  width: 99%;
}

form {
  position: absolute;
  display: inline-block;
}

footer button {
  right: 0;
}

button {
  background: transparent;
  border: none;
  position: absolute;
  transition: all ease 0.2s;
}

button:hover {
  cursor: pointer;
  color: rgb(0, 165, 0);
}

.SignIn button {
  width: 500px;
  position: absolute;
  left: 50%;
  top: 45%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  border: none;
  margin-top: 10px;
  width: 160px;
  -webkit-filter: drop-shadow(0 0 0.25rem rgba(0, 0, 0, 0.2));
          filter: drop-shadow(0 0 0.25rem rgba(0, 0, 0, 0.2));
}

